import { NgModule, OnInit} from '@angular/core';
import { Component } from '@angular/core';
import { ModalController, NavController } from 'ionic-angular';
import { ActionSheetController } from 'ionic-angular';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';
import { Platform } from 'ionic-angular';
import 'rxjs/add/operator/map';
import { AlertController } from 'ionic-angular';
const DATABASE_FILE_NAME: string = 'data.db';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html' 
})
@NgModule({
  imports: [
  ],
  declarations: []
})
export class HomePage implements OnInit{
 user=localStorage.getItem("email")?localStorage.getItem("email"):"anonyme";
  button_value="Ajouter une annonce"
  actif=false;
  nom:String;
  details:String;
  prix:Number;
  timer: number;
  db: SQLiteObject;
  annonces= [];
  items= [];

dbCreated=false;
  constructor(public navCtrl: NavController,    private sqlite: SQLite,private actionSheetController:ActionSheetController
    ,private platform: Platform ,private alertController:AlertController,public modalController: ModalController) {
     
     
       // this.initializeItems();
      
    
  }
  ngOnInit(): void {
    if(!this.dbCreated){
    this.platform.ready().then(() => {
      
      this.createDatabaseFile();
    });
  }
  }


  private createDatabaseFile(): void {
    this.sqlite.create({
      name: DATABASE_FILE_NAME,
      location: 'default'
    })
      .then((db: SQLiteObject) => {
        console.log('Bdd créée !');
        this.db = db;
        this.createTables();
        this.dbCreated=true;
      })
      .catch(e => console.log(e));
  }
  private createTables(): void {

    this.db.executeSql('CREATE TABLE IF NOT EXISTS `ANNONCES` ( `idAnnonce` INTEGER NOT NULL, `name` TEXT NOT NULL,  `desc` TEXT, `prix` INTEGER ,`proprietaire` TEXT, PRIMARY KEY(`idAnnonce`))', {})
      .then(() => {
        console.log('Table Annonces created !');
        this.saveAnnonce1("Maison","une maison à Tunis de 500m de surface est à vendre",555,"essid.nizar.123@gmail.com");
        this.saveAnnonce1("Pc Gamer","un ordinateur gamer utilisé  2 ans ",920,"nizar@gmail.com")
        this.saveAnnonce1("Machine à laver","Bien fonctionnelle",150,"Systeme.hotmail.fr")
        this.initializeItems();
     
      })
      .catch(e => console.log(e));
    
  }

  public initializeItems() {
    
    this.annonces = [];
    this.db.executeSql('SELECT*  FROM `ANNONCES`', {})
      .then((data) => {
        if (data == null) {
          return;
        }
    
        if (data.rows) {
          if (data.rows.length > 0) {
            for (var i = 0; i < data.rows.length; i++) {
              this.annonces.push({"id":data.rows.item(i).idAnnonce,"name":data.rows.item(i).name,"details":data.rows.item(i).desc,"prix":data.rows.item(i).prix,"proprietaire":data.rows.item(i).proprietaire});
            }
            this.items=this.annonces;
          }
      
        };
      });
  }

public ajouterAnnonce()
{ this.button_value="Desactiver l'ajout";
this.actif=!this.actif;}
  public saveAnnonce() {
    console.log('Annonce name -> ' + this.nom);
    console.log('Description -> ' + this.details);

    // INSERT INTO `CATEGORIES` (name) VALUES('Test');
    // INSERT INTO `MOVIES`(name, eval, desc, categoryId) VALUES ('Nom film', 3, 'Description', 1)
    
     
        this.db.executeSql('INSERT INTO `Annonces`(name, desc,prix,proprietaire) VALUES (\'' + this.nom +  '\', \'' + this.details+  '\', \'' +this.prix+  '\', \'' + this.user + '\')', {})
          .then(() => {console.log('Annonce inserted !')
          this.initializeItems();})
          .catch(e => console.log(e));
     
  }

  public saveAnnonce1(nom:String,details:String,prix:Number,proprietaire:String) {
  
        this.db.executeSql('INSERT INTO `Annonces`(name, desc,prix,proprietaire) VALUES (\'' + nom +  '\', \'' + details +   '\', \'' +prix+  '\', \'' +  proprietaire+'\')', {})
          .then(() => console.log('Annonce inserted !'))
          .catch(e => console.log(e));
     
  }
  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();
    this.items=this.annonces;
    // set val to the value of the searchbar
    let val = ev.target.value;
console.log(val);
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.name.toLowerCase().indexOf(val.toLowerCase())>-1);    
       })
    }
    console.log(this.items);
  }








  async presentActionSheet(item:{"id":string,"name":string,"details":string,"prix":Number,"proprietaire":String }) {
    const actionSheet = await this.actionSheetController.create({
      title: 'Options',
      buttons: [{
        text: 'Supprimer',
        role: 'destructive',
        icon: 'trash',
        handler: () => {
          this.supprimer(item.id);
        }
      }, {
        text: 'Modifier',
        icon: 'edit',
        handler: () => {
          this.modifAnnonce({"id":item.id,"name":item.name,"details":item.details,"prix":item.prix });
        }
      },{
        text: 'Cancel',
        icon: 'close',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }]
    });
    await actionSheet.present();
 
  }


  async supprimer(id:String){
   var alert= this.alertController.create({
        title: "Suppression de l'annonce",
        message: `Vous allez supprimer cette annonce ?<br/>Cette opération est irrevrsible`,
        buttons: [
          {
            text:'Annuler',
            role:'annuler',
          },
          {
            text:'Confirmer',
            role:'confirmer',
            handler: ()=>{ 
              this.deleteAnnoce(id) ;
            }
          }
        ]
      })
      alert.present();
}
deleteAnnoce(id:String) {
  console.log(id);
  var sql="DELETE FROM ANNONCES WHERE idAnnonce = " + id;
  console.log(sql);
  this.db.executeSql(sql, [])
    .then((res) => {
      alert("Annonce supprimé avec succès!");
      this.initializeItems();
    })
    .catch(e => {
      alert("error " + JSON.stringify(e))
    });
}


async modifAnnonce(item:{"id":String,"name":string,"details":string,"prix":Number }) {
  const alert = await this.alertController.create({
    
      title: 'Modifier une annonce',
      message: "Enter le nouveau nom de l'annonce",
      inputs: [
        {
          name: 'nom',
          placeholder: item.name,
          value:item.name
        },
        {
          name: 'details',
          placeholder: item.details,
          value:item.details
        },
      ],
      buttons: [
        {
          text: 'Anneuler',
          handler: data => {
           
          }
        },
        {
          text: 'Confirmer',
          handler: data => {
            
            this.edit({"id":item.id,"nom":data.nom,"details":data.details,"prix":data.prix });
          }
        }
      ]
    });
    alert.present();
  }
edit(item:{"id":String,"nom":String,"details":String,"prix":Number}){

  let sqlText = "UPDATE Annonces SET (name , desc,prix ) = ( ? , ? ,?  ) where idAnnonce = ? ;";
  let values = [item.nom, item.details || null,item.prix||null,  item.id] 
  this.db.executeSql(sqlText,values) .then((res) => {
    alert("Annonce modifié avec succès!");
    this.initializeItems();
  })
  .catch(e => {
    alert("error " + JSON.stringify(e))
  });;


}

}



